module.exports = [[module.id, "/* You can add global styles to this file, and also import other style files */\n\n/*=================================================================\nSTEPPER CUSTOM -  CSS START\n========================================================================= */\n\nul.stepper {\n    width: 100%;\n    padding: 5px 0 10px;\n    margin: 0;\n    list-style: none\n}\n\nul.stepper li {\n    min-width: 16%;\n    text-align: center;\n    font-size: 14px;\n    color: #464646;\n    float: left;\n    list-style: none;\n    padding: 40px 0 0;\n    margin: 0;\n    position: relative\n}\n\nul.stepper li a {\n    color: #464646;\n    background-color: inherit\n}\n\nul.stepper li.active a {\n    font-weight: 500;\n    color: #002f60\n}\n\nul.stepper li span {\n    width: 28px;\n    height: 28px;\n    display: block;\n    margin: 0 auto;\n    vertical-align: middle;\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    z-index: 2;\n    content: '';\n    background-color: #979797;\n    border-radius: 100%;\n    -webkit-border-radius: 100%;\n    -moz-border-radius: 100%\n}\n\nul.stepper li span img {\n    width: 17px;\n    line-height: 28px;\n    display: block;\n    margin: 8px auto 0\n}\n\nul.stepper li:after {\n    width: 100%;\n    height: 1px;\n    background: #c4c4c4;\n    display: block;\n    position: absolute;\n    top: 14px;\n    left: -50%;\n    z-index: 1;\n    content: ''\n}\n\nul.stepper li:first-child:after {\n    display: none\n}\n\nul.stepper li span.bg-success {\n    background: url('icon-stepper-tick-light.svg') no-repeat center #002f60\n}\n\n@media(max-width:640px) {\n    ul.stepper {\n        padding: 10px 0 10px 73px\n    }\n    ul.stepper li {\n        width: 100%;\n        text-align: left;\n        padding: 0 0 45px 40px\n    }\n    ul.stepper li:last-child {\n        padding-bottom: 0px\n    }\n    ul.stepper li span {\n        right: inherit\n    }\n    ul.stepper li:after {\n        width: 1px;\n        height: 45